.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 100%;
    height: 100%;
    color: black;
    font-size: 2rem;
}

.map-container {
    display: grid;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 100%;
    height: 100%;
    color: black;
    font-size: 2rem;
}

.grid-container {
    display: grid;
    grid-auto-flow: column;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fill, 49px);
    grid-template-rows: repeat(10, 40px);
    background-color: #2196F3;
    padding: 5px;
}

.grid-item {
    background-color: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 1px;
    font-size: 10px;
    text-align: center;
    width: 45px;
    height: 40px;
}